@mixin clearfix() {display: table; clear: both; content: "";}
@mixin hide1() {position: absolute; left: -1000em;}

// variables

$gutter: 1.25rem;

$pw: 375px;
$tw: 768px;
$dw: 960px;
$dlw: 1400px;
$cw: 1680px;

// mixins

@mixin phone {
  @media (max-width: #{$pw - 1}) {
    @content;
  }
}

@mixin phone-l {
  @media (min-width: #{$pw}) {
    @content;
  }
}

@mixin tablet-s {
  @media (min-width: #{$tw - 150}) {
    @content;
  }
}

@mixin tablet-l {
  @media (min-width: #{$tw + 1}) {
    @content;
  }
}

@mixin desktop {
  @media (min-width: #{$dw}) {
    @content;
  }
}

@mixin desktop-l {
  @media (min-width: #{$dlw}) {
    @content;
  }
}

@mixin cinema {
  @media (min-width: #{$cw + 1}) {
    @content;
  }
}