@font-face {
  font-family: 'icomoon';
  src:url('/assets/fonts/icomoon/icomoon.eot?yj3l8l');
  src:url('/assets/fonts/icomoon/icomoon.eot?yj3l8l#iefix') format('embedded-opentype'),
    url('/assets/fonts/icomoon/icomoon.ttf?yj3l8l') format('truetype'),
    url('/assets/fonts/icomoon/icomoon.woff?yj3l8l') format('woff'),
    url('/assets/fonts/icomoon/icomoon.svg?yj3l8l#icomoon') format('svg');
  font-weight: normal;
  font-style: normal;
}

[class*="icon-"]:before {
  font-family: 'icomoon' !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1.0;
}

p a[class*="icon-"]:before {margin-right: 5px;}

