/* ------------------------------- */
/* =Structure */
/* ------------------------------- */

html,
body,
footer[role=contentinfo] {background: #f0f0f0;}
  
body {
  text-align: left;
  font: 100%/1.4 $sansSerif;
  color: $text;

  @include tablet-l {font-size: 112.5%;}
}

.wrap {
  margin: 0 auto;
  padding: 0 ($gutter);
  max-width: 70em;
  box-sizing: border-box;

  @include tablet-l {padding: 0 ($gutter * 2);}

  main & {
    padding-top: 2.5rem;
    padding-bottom: 2.5rem;
  }
}

// grid

.row {
  margin-left: -$gutter; margin-right: -$gutter;

  @include tablet-l {
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
  }

  > .col {
    padding: 0 $gutter;

    @include tablet-l {
      flex: 1 1 auto;
      padding: 0 $gutter;

      &.col-1 {width: 8.333%;}
      &.col-2 {width: 16.666%;}
      &.col-3 {width: 25%;}
      &.col-4 {width: 33.333%;}
      &.col-5 {width: 41.666%;}
      &.col-6 {width: 50%;}
      &.col-7 {width: 58.333%;}
      &.col-8 {width: 66.666%;}
      &.col-9 {width: 75%;}
      &.col-10 {width: 83.333%;}
      &.col-11 {width: 91.666%;}
    }
  }
}

/* ------------------------------- */
/* =Header */
/* ------------------------------- */

header[role="banner"] {
  position: relative;
  z-index: 2;
  border: solid $quaternary;
  border-width: 0 0 0.75em;
  background: white;

  @include tablet-l {border-bottom: 0; border-top-width: 0.75em;}

  div.logo.wrap {
    position: relative;
    z-index: 1;

    padding-top: 2.8rem;
    @include tablet-l {padding-top: 0;}

    h1 {
      margin: 1.5rem auto;
      max-width: 250px;

      @include tablet-l {
        float: left;
        margin-top: 3.5rem;
        max-width: 250px;
      }

      @include desktop {
        margin-top: 1.5rem;
      }

      body.home & {
        max-width: 300px;
        @include tablet-l {max-width: 350px;}
      }

      a {
        display: block;
        outline: none;

        img {
          display: block;
          width: 100%;
          transition: all 0.5s;
        }
      }
    }

    div.contact {
      position: fixed;
      top: 0; left: 0; right: 0;
      z-index: 2;
      padding: 0.65em $gutter;
      background: $primary;
      text-align: left;
      text-transform: uppercase;
      color: white;
      box-shadow: 0 2px 2px rgba(0,0,0,0.2);

      @include tablet-l {
        position: absolute;
        left: auto; right: ($gutter * 2);
        border-radius: 0 0 0.5em 0.5em;
        padding: 0 1em 0.5em;
        background: $quaternary;
        text-align: right;
        color: #ddd;
        box-shadow: none;
      }

      > div {
        display: inline-block;
        vertical-align: middle;
      }

      div.email {
        @include hide1();
        margin-left: 0.6em;
        border-left: 2px solid rgba(255,255,255,0.075);
        padding-left: 1em;
        font-size: 0.8em;

        @include tablet-l {position: static;}
      }

      div.numbers {

        dl {
          text-align: right;

          dt {
            @include hide1();
            margin-bottom: 0.2em;
            font-size: 0.8em;

            @include tablet-l {position: static;}
          }

          dd {
            display: inline-block;

            &:first-of-type {margin-right: 0.75em;}

            &:last-of-type {
              @include hide1();
              @include tablet-l {position: static;}
            }

            a.tel {
              display: block;
              font-size: 1.1em;
              line-height: 1.2;
              color: white;
              text-transform: none;

              &:before {
                margin-right: 0.5em;
                font-size: 0.8em;
              }
            }
          }
        }
      }
    }
  }
}

/* ------------------------------- */
/* =Navigation */
/* ------------------------------- */

nav[role="navigation"] {
  position: fixed;
  top: 0; right: 0;
  z-index: 3;
  clear: both;
  text-align: center;
  font-size: 1.1em;
  text-transform: uppercase;

  @include tablet-l {
    position: static;
    background: $primary;
  }

  h2 {
    display: block;
    position: absolute;
    top: 0; right: 0;
    z-index: 4;
    font-size: 1em;
    text-align: center;
    transition: right 0.15s, top 0.15s;

    @include tablet-l {right: auto; left: -1000em;}

    a {
      display: block;
      position: relative;
      padding: 0 $gutter;
      height: 2.55em;
      text-align: center;
      line-height: 2.75em;
      font-weight: 400;
      color: white;
      box-sizing: border-box;
      outline: none;

      span {
        display: inline-block;
        margin-left: 0.5em;
        @include tablet-l {@include hide1();}
      }
    }
  }

  div.additional-nav {
    position: fixed;
    left: 0; right: 0; bottom: 0;
    z-index: 4;
    text-align: center;
    opacity: 0;
    transform: translate3d(0, -80px, 0);
    transition: transform 0.2s, opacity 0.2s;

    @include tablet-l {@include hide1();}

    &.show {
      opacity: 1;
      transform: translate3d(0, 0, 0);
      transition: transform 0.5s, opacity 0.5s;
      transition-delay: 0.1s;
    }

    ul.social {
      margin: 2em 0;
      font-size: 1.1em;

      @include phone-l {font-size: 1.3em;}
    }
  }

  > ul {
    position: fixed;
    top: -1500px; left: 0; right: 0; bottom: 100%;
    z-index: 3;
    padding: 1.1em 0 0;
    background: rgba(black, 0.85);
    text-align: center;
    font-size: 1em;
    text-transform: uppercase;
    opacity: 0;
    transition: opacity 0.5s, top 0s 0.5s, bottom 0s 0.5s;

    .nav-open & {
      top: 0; bottom: 0;
      opacity: 1;
      transition: opacity 0.5s;
    }

    @include phone-l {font-size: 1.3em;}

    @include tablet-l {
      position: static;
      border-radius: 0;
      padding: 1em 0;
      background: none;
      font-size: 0.9em;
      text-align: center;
      opacity: 1;
    }

    li {
      display: block;
      white-space: nowrap;
      opacity: 0;
      transform: translate3d(0, -80px, 0);
      transition: transform 0.2s, opacity 0.2s;

      .nav-open & {
        opacity: 1;
        transform: translate3d(0, 0, 0);
        transition: transform 0.5s, opacity 0.5s;
        transition-delay: 0.025s;

        &:nth-child(2) {transition-delay: 0.05s;}
        &:nth-child(3) {transition-delay: 0.075s}
        &:nth-child(4) {transition-delay: 0.1s}
        &:nth-child(5) {transition-delay: 0.125s}
        &:nth-child(6) {transition-delay: 0.125s}
      }

      &.current a {color: $primary;}

      @include tablet-l {
        display: inline-block;
        margin: 0;
        opacity: 1;
        transform: none;

        &.current a {background: rgba(0,0,0,0.1); color: white;}
      }

      a {
        position: relative;
        display: inline-block;
        padding: 0.5em 1em;
        color: white;
        outline: none;
        transition: color 0.3s;

        @include tablet-l {
          display: block;
          border-radius: 3px;
          padding: 0.5em 0.75em;

          &:hover {background: rgba(0,0,0,0.05);}          
        }

        span {
          @include hide1();
          @include desktop {position: static;}
        }
      } 
    }
  }
}

/* ------------------------------- */
/* =Main */
/* ------------------------------- */

main[role="main"] {
  position: relative;
  z-index: 1;
  background: white;

  div.strip {
    background: rgba(black,0.05);
    
    div.row > div:last-child > :last-child {margin-bottom: 0;}
  }
}

/* ------------------------------- */
/* =Footer */
/* ------------------------------- */
  
footer[role="contentinfo"] {
  position: relative;
  z-index: 1;
  padding: 0 0 2em;
  text-align: center;

  ul.affiliates {
    padding: 2rem 0;
    background: rgba(255,255,255,0.5);

    li {
      display: inline-block;
      vertical-align: middle;
      margin: 0 0.5em;
      list-style: none;

      @include tablet-l {font-size: 1.2em;}

      a {
        display: block;

        img {
          display: block;
          width: 4em;
        }
      }

      span {display: block;}
    }
  }

  div.info {
    margin: 2em 0 1em;

    p {font-size: 0.9em;}
    
    span.adr span {white-space: nowrap;}

    b.org,
    span.copyright {
      display: block;
      @include tablet-l {display: inline;}
    }
  }

  p.credit {font-size: 0.8em;}
}

/* ------------------------------- */
/* =Lists */
/* ------------------------------- */

ul.unstyled,
ol.unstyled {
  margin-left: 0;
  list-style: none;
}

ul.spaced li {margin-bottom: 0.3em;}

ul.check {
  list-style: none;

  li {
    position: relative;
    margin: 0.6rem 0;
    padding-left: 0.3rem;
    line-height: 1.2;

    &:before {
      position: absolute;
      top: 0.4rem; right: 100%;
      margin-right: 0.3rem;
      font-size: 0.7rem;
      color: $primary;
    }
  }
}

li[class*="icon"] {
  list-style: none!important;
}

li[class*="icon"]:before {
  position: absolute;
  top: 0.3rem; right: 100%;
  margin-right: 0.3rem;
  font-size: 0.8em;
  color: $primary;
}

ul.testimonials {
  margin-left: 0;
  list-style: none;

  li {

    blockquote {
      border-radius: 0.5em;
      padding: 1.5rem 2rem;
      background: #f2f2f2;

      .strip & {background: white;}
    }
      
    &:last-child blockquote {margin-bottom: 0;}
    
    p.attribution {
      margin: 0.5em 0 0;
      font-size: 0.8em;
      font-style: italic;

      b {
        display: block;
        font-style: normal;
      }
    }
  }
}

ul.social {
  margin: 1em 0;
  font-size: 1.3em;

  li {
    display: inline-block;
    margin: 0 0.25em;
    list-style: none;

    a {
      display: block;
      border-radius: 50%;
      width: 2.5em;
      height: 2.5em;
      line-height: 2.7;
      background: $text;
      color: white;

      &.icon-facebook {background: #3b5998;}
      &.icon-twitter {background: #55acee;}

      span {@include hide1();}
    }
  }
}

ul.phone-numbers {
  margin-left: 0;
  list-style: none;

  li a:before {
    display: inline-block;
    margin-right: 0.35em;
    min-width: 1em;
    text-align: center;
    font-size: 0.7em;
    color: $text;
  }
}

/* ------------------------------- */
/* =Tables */
/* ------------------------------- */

table {
  border-spacing: 0 0;
  border-collapse: collapse;
  border: 0;
  width: 100%;

  th,td {
    border: solid #ddd;
    border-width: 0 1px 1px 1px;
    padding: 10px;
    text-align: left;
    font-weight: normal;
    vertical-align: middle;

    small {display: block;}
  }
    
  thead th {
    border-color: $tertiary;
    border-right-color: rgba(255,255,255,0.05);
    background: $quaternary;
    color: #fff;
    font-weight: bold;
    vertical-align: bottom;

    &:last-child {
      border-right-color: $tertiary;
      width: 6rem;
      @include phone-l {width: 10rem;}
    } 
  }

  tr.alt {

    th, td {
      border-color: #ccc;
      border-bottom-width: 0;
      background: #e5e5e5;
      vertical-align: middle;
    }
  }
}
