div.buttons {margin: 0.5em 0 1.5em;}

.button { 
  display: inline-block;
  border-radius: 3px;
  padding: 0.75rem 1rem;
  background: $primary;
  text-align: center;
  font-weight: 700;
  color: #fff!important;
  cursor: pointer;
  outline: none;
  text-decoration: none;
  text-transform: uppercase;
  -webkit-appearance: none;

  &:hover {background: rgba($primary,0.9);}
}